@font-face {
  font-family: Kumbh sans;
  src: url("KumbhSans-Regular.0c70b83f.ttf");
  font-weight: normal;
}

@font-face {
  font-family: Kumbh sans;
  src: url("KumbhSans-Bold.4a519480.ttf");
  font-weight: bold;
}

@font-face {
  font-family: Roboto Slab;
  src: url("RobotoSlab.82bacfb3.ttf");
}

@font-face {
  font-family: Space Mono;
  src: url("SpaceMono-Regular.941fdf57.ttf");
  font-weight: normal;
}

@font-face {
  font-family: Space Mono;
  src: url("SpaceMono-Bold.ff5d2d6a.ttf");
  font-weight: bold;
}

:root {
  --clr-red: #f87070;
  --clr-cyan: #70f3f8;
  --clr-purple: #d881f8;
  --clr-white-blue: #fff6d7;
  --clr-dark-blue: #1e213f;
  --clr-white: #fff;
  --clr-gray: #eff1fa;
  --clr-dark-gunmetal: #161932;
  --clr-accent: var(--clr-red);
  --font: "Kumbh sans";
}

* {
  box-sizing: border-box;
  font-family: var(--font), sans-serif;
  margin: 0;
  font-weight: bold;
}

body {
  background-color: var(--clr-dark-gunmetal);
  color: var(--clr-white);
  height: 100vh;
  grid-template-rows: auto 1fr 1fr;
  margin: 0;
  padding: 0;
  display: grid;
}

main {
  max-width: 373px;
  grid-auto-rows: auto 1fr;
  gap: 48px;
  margin: 0 auto auto;
  display: grid;
}

footer {
  justify-content: center;
  align-items: center;
  display: flex;
}

h1 {
  letter-spacing: -5px;
  font-size: 100px;
  line-height: 120px;
}

h2 {
  font-size: 28px;
  line-height: 34px;
}

h3 {
  letter-spacing: 15px;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 19px;
}

h4 {
  letter-spacing: 5px;
  text-transform: uppercase;
  font-size: 13px;
  line-height: 16px;
}

.body-1 {
  font-size: 14px;
  line-height: 18px;
}

.body-2 {
  font-size: 12px;
  line-height: 14px;
}

button {
  background-color: var(--clr-red);
  color: var(--clr-white);
  cursor: pointer;
  border-color: #0000;
  border-radius: 26.5px;
  padding: 16px 48px;
  font-size: 16px;
}

button:hover {
  filter: brightness(120%);
}

label {
  color: var(--clr-dark-blue);
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
}

input, select {
  color: var(--clr-dark-blue);
  background-color: var(--clr-gray);
  height: 40px;
  border-color: #0000;
  border-radius: 10px;
  padding-left: 16px;
  font-size: 14px;
  font-weight: bold;
}

header h1 {
  text-align: center;
  letter-spacing: 0;
  font-size: 24px;
}

section#timer-type-selection {
  z-index: 100;
  background-color: var(--clr-dark-gunmetal);
  border-radius: 32px;
  justify-content: space-evenly;
  align-items: center;
  padding: 8px 6px;
  display: flex;
  position: relative;
}

section#timer-type-selection > label {
  color: var(--clr-white);
  cursor: pointer;
  opacity: .4;
  padding: 18px 24px;
  font-size: 12px;
}

section#timer-type-selection > input[type="radio"]:checked + label {
  color: var(--clr-dark-blue);
  background-color: var(--clr-accent);
  opacity: 1;
  border-radius: 26.5px;
}

section#timer-type-selection > input[type="radio"] {
  display: none;
}

section#timer {
  justify-content: center;
  align-items: center;
  display: flex;
}

section#timer > button {
  width: 95%;
  max-width: 350px;
  aspect-ratio: 1;
  border-color: var(--clr-dark-gunmetal);
  cursor: pointer;
  letter-spacing: 13px;
  text-transform: uppercase;
  text-align: center;
  background: linear-gradient(315deg, #2e325a 0%, #0e112a 100%);
  border-radius: 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  display: flex;
  position: relative;
  box-shadow: -50px -50px 100px #272c5a, 50px 50px 100px #121530;
}

section#timer > button > svg {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

section#timer #progress-ring {
  z-index: 60;
  stroke: var(--clr-accent);
  stroke-linecap: round;
  stroke-width: 8px;
  fill: var(--clr-dark-gunmetal);
  transform-origin: 50%;
  position: relative;
  transform: rotate(-90deg);
}

section#timer #progress-ring-wrapper {
  z-index: 59;
  stroke-width: 16px;
  stroke: var(--clr-dark-gunmetal);
  fill: var(--clr-dark-gunmetal);
  transform-origin: 50%;
  position: relative;
  transform: rotate(-90deg);
}

section#timer time {
  z-index: 61;
  width: 100%;
  letter-spacing: -4px;
  grid-template-columns: 1fr .25fr 1fr;
  align-items: center;
  font-size: 80px;
  display: grid;
  position: relative;
}

section#timer time > span:first-child {
  text-align: end;
}

section#timer time > span:nth-child(2) {
  text-align: center;
}

section#timer time > span:last-child {
  text-align: start;
}

section#timer button #timer-button-label {
  z-index: 60;
  text-align: center;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: -13px;
  position: relative;
}

#settings-dialog-toggle {
  background-color: #0000;
  margin: auto;
  display: block;
}

dialog {
  min-width: 327px;
  max-width: 540px;
  overflow: unset;
  border-color: #0000;
  border-radius: 15px;
  margin: auto;
  position: absolute;
  inset: 0;
}

dialog header {
  height: 72px;
  border-bottom: 1px solid #e3e1e1;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

dialog h1 {
  color: var(--clr-dark-gunmetal);
  letter-spacing: 0;
  font-size: 20px;
  line-height: 24px;
}

dialog button#close-dialog {
  width: auto;
  height: auto;
  background-color: #0000;
  justify-content: center;
  align-items: center;
  padding: 8px;
  display: flex;
}

dialog button#close-dialog svg {
  height: auto;
  aspect-ratio: 1;
}

dialog form {
  flex-direction: column;
  display: flex;
}

dialog form fieldset {
  border-color: #0000;
  padding-bottom: 24px;
}

dialog form fieldset:not(:last-child) {
  border-bottom: 1px solid #e3e1e1;
}

dialog form input[type="radio"] {
  display: none;
}

dialog form input[type="radio"] + label {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  justify-content: center;
  justify-self: center;
  align-items: center;
  font-size: 15px;
  display: flex;
  position: relative;
}

dialog form fieldset[name="time"] {
  grid-template-rows: repeat(3, 1fr);
  align-items: center;
  gap: 8px;
  display: grid;
}

dialog form fieldset[name="time"] label {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  display: grid;
}

dialog form fieldset legend {
  width: auto;
  letter-spacing: 4px;
  text-transform: uppercase;
  grid-column: 1 / 2;
  margin: 25px auto 18px;
  font-size: 11px;
}

dialog form fieldset > div {
  flex-direction: row;
  justify-content: center;
  justify-self: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

dialog form fieldset[name="font"] label {
  background-color: var(--clr-gray);
  color: var(--clr-dark-blue);
}

dialog form fieldset[name="font"] input[type="radio"]:checked + label {
  background-color: var(--clr-dark-gunmetal);
  color: var(--clr-white);
}

dialog form fieldset[name="font"] label[for="font-input-kumbh-sans"] {
  font-family: Kumbh Sans, sans-serif;
}

dialog form fieldset[name="font"] label[for="font-input-roboto-slab"] {
  font-family: Roboto Slab, sans-serif;
}

dialog form fieldset[name="font"] label[for="font-input-space-mono"] {
  font-family: Space Mono, sans-serif;
}

dialog form fieldset[name="accent-color"] {
  padding-bottom: 32px;
}

dialog form fieldset[name="accent-color"] label[for="accent-color-red"] {
  background-color: var(--clr-red);
}

dialog form fieldset[name="accent-color"] label[for="accent-color-cyan"] {
  background-color: var(--clr-cyan);
}

dialog form fieldset[name="accent-color"] label[for="accent-color-purple"] {
  background-color: var(--clr-purple);
}

dialog form fieldset[name="accent-color"] input[type="radio"]:checked + label:before {
  height: 10px;
  width: 3px;
  background-color: var(--clr-dark-gunmetal);
  content: "";
  transform-origin: 0 100%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateX(15px)rotate(-45deg);
}

dialog form fieldset[name="accent-color"] input[type="radio"]:checked + label:after {
  height: 3px;
  width: 55%;
  background-color: var(--clr-dark-gunmetal);
  content: "";
  transform-origin: 0 100%;
  position: absolute;
  bottom: 10px;
  left: 0;
  transform: translateX(15px)rotate(-45deg);
}

dialog button[type="submit"] {
  margin: auto;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  dialog form fieldset[name="time"] {
    max-width: 100%;
    grid-template-rows: unset;
    grid-template-columns: repeat(3, 1fr);
  }

  dialog form fieldset[name="time"] legend {
    margin-left: 0;
    font-size: 13px;
  }

  dialog form fieldset[name="time"] label {
    grid-template-columns: unset;
    width: 100%;
    grid-template-rows: auto 1fr;
  }

  dialog form fieldset[name="time"] label * {
    width: 100%;
  }

  dialog form fieldset[name="font"], dialog form fieldset[name="accent-color"] {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    padding: 24px 12px;
    display: grid;
  }

  dialog form fieldset[name="font"] legend, dialog form fieldset[name="accent-color"] legend {
    margin: 0;
    display: contents;
  }
}

